import React, { PureComponent } from "react";
import styled, { css, keyframes } from "styled-components";

const Button = styled.button`
  border: 0;
  outline: 0;
  background-color: transparent;
  cursor: pointer;
`;

const slide = keyframes`
  0 {
    transform: translate3d(0, 0, 0);
  }
  100% {
    transform: translate3d(0, -26px, 0);
  }
`;

const pulsing2 = keyframes`
 0% {transform: scale(1);}
  25% {transform: scale(.97);}
  35% {transform: scale(.9);}
  45% {transform: scale(1.1);}
  55% {transform: scale(.9);}
  65% {transform: scale(1.1);}
  75% {transform: scale(1.03);}
  100% {transform: scale(1);}
`;

const heartbeat = keyframes`
  0% { 
    opacity: 1;
    transform: scale(0.3);
    transform: translate3d(0, 0, 0);
  }

  5% {
    opacity: 1;
    transform: scale(1.6);
  }



  100% {
    opacity: 0;
    transform: scale(0);
    transform: translate3d(-180px, -220px, 0);
  }
`;

const blowout = keyframes`
  0% {
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
`;

const confetti = keyframes`
  0% {
    opacity: 1;
  }
  100% {
    transform: scale(1.1);
    opacity: 1;
  }
`;

const Container = styled.span`
  height: 16px;
  position: relative;
  display: flex;
  align-items: center;
  // background-color: #eaeaea;
  padding: 6px 10px 4px 10px;
  cursor: pointer;

  &:before {
    content: "";
    position: absolute;
    width: 27px;
    height: 27px;
    margin-top: -1px;
    margin-left: -5px;
    border-radius: 100%;
    background: #d83535;
    display: inline-block;
    transform: scale(0);
    will-change: transform;
  }

  &:after {
    content: "";
    position: absolute;
    top: 11px;
    left: 16px;
    width: 2px;
    height: 2px;
    border-radius: 2px;
    box-shadow: 0 -18px 0 #e96132, 12px -12px 0 #e96132, 18px 0 0 #e96132,
      12px 12px 0 #e96132, 0 18px 0 #e96132, -12px 12px 0 #e96132,
      -18px 0 0 #e96132, -12px -12px 0 #e96132;
    transform: scale(0);
    will-change: transform;
  }

  svg {
    fill: #b2b2b2;
    will-change: transform;
  }

  ${(props) =>
    props.hearted &&
    css`
      &:before {
        animation: ${blowout} 0.4s ease-in-out;
      }

      svg {
        opacity: 0;
        fill: red;
        animation: ${heartbeat} 2s ease-out forwards;
        animation-delay: 0.2s;
      }

      &:after {
        animation: ${confetti} 0.5s ease-in-out;
        animation-delay: 0.1s;
      }
    `};

  ${(props) =>
    !props.hearted &&
    css`
      &:hover {
        svg {
          stroke: #e96132;
          fill: #e96132;
          animation: ${pulsing2} 2.5s infinite;
          filter: drop-shadow(0px 1px 10px #e96132);
        }
      }
    `}
`;

class Heart extends PureComponent {
  render() {
    return (
      <Button onClick={this.props.onClick}>
        <Container hearted={this.props.hearted}>
          <svg xmlns="http://www.w3.org/2000/svg" width="17" height="15">
            <path d="M12.4713 0C10.7596 0 9.2698.95 8.5 2.3512 7.73.95 6.2404 0 4.5287 0 2.0274 0 0 2.0277 0 4.5287c0 5.2882 8.5 9.824 8.5 9.824S17 9.817 17 4.529C17 2.0277 14.9726 0 12.4713 0z" />
          </svg>
          <div>Words can't express enough for how sorry I am...</div>
        </Container>
      </Button>
    );
  }
}

export default Heart;



